import { Plugin } from '@ali/4ever-cangjie';
import { ColumnsPlugin } from '@ali/4ever-bamboo';
import { OldBambooPlugin } from '@ali/4ever-plugin-columns';

const { hooks } = ColumnsPlugin;

function createColumnsPlugin(allConfigs): Plugin {
  const { createRenderNode } = hooks;
  const configs = allConfigs.columns;
  return {
    renderNode: createRenderNode(configs),
    beforeCopySelectedNodes: hooks.beforeCopySelectedNodes,
    queries: OldBambooPlugin.createQueries(configs),
  };
}

export default createColumnsPlugin;
